body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.player
{
  border: none;  
  padding: 10px;
}

.btn_span
{
  text-decoration: underline;
  cursor: pointer;
}

.btn_span2
{
  text-decoration: none;
  cursor: pointer;
}


.container {
  display: flex;
  flex-direction: column; /* 위에서 아래로 요소 배치 */
  padding: 10px;
  min-height: 100%;
}

.top-div {
  height: 100%; /* 남은 공간을 모두 차지하도록 설정 (선택 사항) */  
  margin-bottom: 300px; 
}

.playlist-div {
  background-color: #FFFBDE;
  border: solid 1px;
  padding: 3px;
  position: fixed;
  left: 0;
  bottom: 100px;
  width: 100%;
  height: 200px;
  overflow-y: auto;       /* 내용이 넘칠 때만 세로 스크롤 생성 */
}

.bottom-div {
  background-color: #90D1CA;
  border: none;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100px;
  /* 높이 설정 (선택 사항) */
  /* 기타 스타일 (선택 사항) */
}

.playing_item
{
  background-color: #90D1CA;
  color: black;
  border: solid 0px;  
  width: 100%;
  padding: 5px;  
  font-weight: bold;
}

.no_playing_item
{  
 text-decoration: none;
 padding: 5px;
}

.button2
{
  padding: 10px;
  background-color: #096B68;
  border: none;
  color: white;
  margin-bottom: 5px;
}


.container_playlist_buttons
{
  display: flex;
  flex-direction: row; /* 가로 방향으로 배치 */
  justify-content: space-between; /* 양쪽 끝으로 정렬 */
  align-items: center; /* 세로 중앙 정렬 */
  margin-bottom: 5px;
}
